<template>
    <div class="m-rank-list">
        <div class="m-rank-edit-icon" v-if="isEdit" @click="dialogFormVisible = true">
            <el-icon><Setting /></el-icon>
        </div>
        <el-collapse v-model="activeNames" @change="handleChange" accordion>
            <el-collapse-item
                :title="item['label']"
                :name="item['label']"
                v-for="(item, index) in rankList"
                :key="index"
            >
                <div class="m-rank-cont-list" v-for="(eitem, eindex) in toJson(item.content)" :key="eindex">
                    <div class="m-rank-cont-item">
                        <div class="m-rank-cont-img">
                            <img :src="getImgToName(eitem.name)" alt="" srcset="" />
                        </div>
                        <div class="m-rank-cont-text">{{ eitem.name }}</div>
                        <div class="m-rank-cont-num">{{ eitem.num }}</div>
                    </div>
                </div>
            </el-collapse-item>
        </el-collapse>

        <el-dialog v-model="dialogFormVisible" title="" center destroy-on-close>
            <el-form>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select
                        v-model="labelVal"
                        filterable
                        allow-create
                        default-first-option
                        :reserve-keyword="false"
                        placeholder=""
                        @change="changeRankItem"
                    >
                        <el-option
                            v-for="item in labelOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        />
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="client" class="m-2" placeholder="Select" size="large">
                        <el-option
                            v-for="item in clientOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                        />
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-switch v-model="status" />
                </el-form-item>
            </el-form>

            <div class="m-rank-form">
                <div class="m-left"></div>
                <div class="m-right">
                    <div class="m-rank-form-header">
                        <div></div>
                        <div></div>
                    </div>
                    <div class="m-rank-form-cont">
                        <draggable
                            :list="content"
                            ghost-class="ghost"
                            chosen-class="chosenClass"
                            animation="300"
                            itemKey="index"
                        >
                            <template #item="{ element, index }">
                                <div class="item-list" :key="element.id">
                                    <el-input placeholder="" v-model="element.name" autocomplete="off" />
                                    <el-input placeholder="" v-model="element.num" autocomplete="off" />

                                    <div class="m-rank-form-icon" @click="delRankItem(index)">
                                        <el-icon><DeleteFilled /></el-icon>
                                    </div>
                                </div>
                            </template>
                        </draggable>
                        <div class="rank-item-create" @click="addRankItem()">
                            <el-icon><Plus /></el-icon>
                        </div>
                    </div>
                </div>
            </div>

            <template #footer>
                <span class="dialog-footer">
                    <el-button @click="delRankList()"></el-button>
                    <el-button type="primary" @click="saveRankList()">  </el-button>
                </span>
            </template>
        </el-dialog>
    </div>
</template>
<script>
import draggable from "vuedraggable";
import { getRankList, createRankItem, putRankList, delRankList } from "@/service/raw.js";
import school from "@jx3box/jx3box-data/data/xf/school.json";
import { showSchoolIcon } from "@jx3box/jx3box-common/js/utils";
import User from "@jx3box/jx3box-common/js/user.js";

export default {
    name: "SkillItem",
    components: {
        draggable,
    },
    data: function () {
        return {
            dialogFormVisible: false,
            activeNames: "1",
            status: true, // number  0  1 
            label: "", //
            formLabelWidth: "120",
            client: "std",
            content: [
                {
                    name: "",
                    num: "1",
                    id: 0,
                },
            ],
            labelVal: "",
            labelOptions: [],
            clientOptions: [
                {
                    label: "",
                    value: "std",
                },
                {
                    label: "",
                    value: "origin",
                },
            ],
            rankList: [],
            thatRankId: 0,
        };
    },
    computed: {
        isEdit() {
            return User.isEditor();
        },
    },
    methods: {
        async getRankList() {
            let data = await getRankList({
                client: this.client,
            });
            this.rankList = data.data.reverse();
            data.data.forEach((res, index) => {
                this.labelOptions[index] = {
                    label: res.label,
                    value: res.label,
                };
            });
        },
        handleChange() {},
        addRankItem() {
            this.content[this.content.length] = {
                name: "",
                num: "",
            };
        },
        delRankItem(index) {
            this.content.splice(index, 1);
        },
        // 
        async createRankItem() {
            let data = {
                client: this.client,
                status: this.status ? 1 : 0,
                label: this.labelVal,
                content: JSON.stringify(this.content || []),
            };
            let res = await createRankItem(data);
            if (res) {
                this.dialogFormVisible = false;
                this.$message({ type: "success", message: "" });
                this.getRankList();
            }
        },

        changeRankItem(val) {
            for (let i = 0; i < this.labelOptions.length; i++) {
                const el = this.labelOptions[i];
                if (el.value == val) {
                    this.loadRankItem(val);
                    return false;
                }
            }
            this.thatRankId = 0;
            this.content = [
                {
                    name: "",
                    num: "1",
                    id: 0,
                },
            ];
            // this.createRankItem();
        },
        //  
        loadRankItem(val) {
            let thatItem = this.rankList.find(function (item) {
                return val == item.label;
            });
            this.client = thatItem.client || "";
            this.content = JSON.parse(thatItem.content || []);
            this.status = thatItem.status ? 1 : 0;
            this.labelVal = thatItem.label;
            this.thatRankId = thatItem.id;
        },
        async delRankList() {
            let res = await delRankList(this.thatRankId);
            if (res) {
                this.dialogFormVisible = false;
                this.$message({ type: "success", message: "" });
                this.getRankList();
            }
        },
        // 
        async saveRankList() {
            let data = {
                client: this.client,
                status: this.status ? 1 : 0,
                label: this.labelVal,
                content: JSON.stringify(this.content),
            };
            if (!this.thatRankId) {
                console.log("");
                this.createRankItem();
                return false;
            }
            let res = await putRankList(this.thatRankId, data);
            if (res) {
                this.dialogFormVisible = false;
                this.$message({ type: "success", message: "" });
                this.getRankList();
            }
        },

        toJson(str) {
            return JSON.parse(str ? str : "[]");
        },
        getImgToName(name) {
            let schoolId = school[name] ? school[name].school_id : 1;
            return showSchoolIcon(schoolId);
        },
    },
    created() {
        this.getRankList();
    },
};
</script>
<style lang="less" scoped>
@import "@/assets/css/rank-list.less";
</style>
<style lang="less">
.m-rank-list {
    .el-collapse-item__header {
        font-size: 18px;
        padding-left: 24px;
    }
    .el-dialog__body {
        width: 500px;
        margin: 0 auto;
        .el-select {
            width: 100%;
        }
        .m-rank-form-cont {
            .item-list {
                .el-input {
                    margin-right: 15px;
                }
            }
        }
    }
}
</style>
